body{
    background-color: #ffeaa7;
   
       min-height: 70vh;
       padding: 1rem;
       box-sizing: border-box;
       display: flex;
       justify-content: center;
       align-items: center;
       color: hsl(198, 1%, 29%);
       font-family: "Architects Daugther", cursive;
       text-align: center;
       font-size: 130%;
   }
   .container{
       
    width: 100%;
    height: auto;
    max-width: 500px;
    min-width: 250px;
    min-height: 500px;  
    background-color:#f1f5f8 ;
    background-image: radial-gradient(#bfc0c1 7.2%,transparent 0);
    background-size: 25px 25px;
    border-radius: 20px;
    box-shadow: 4px 3px 7px 2px #00000040;
    padding: 1rem;
    box-sizing: border-box;
   }
   .heading{
       display: flex;
       align-items: center;
       justify-content: center;
       margin-bottom: 1rem;
   }
   .heading >h1{
       transform: rotate(2deg);
       padding: 0.2rem 1.2 rem;
       border-radius: 20% 5% 20% 5% / 5% 20% 25% 20%;
       background-color: #fdcd6e;
       font-size: 1.5rem;
   
   }
   .form > input{
       box-sizing: border-box;
       background-color: transparent;
       padding: 0.7rem;
       border-bottom-right-radius: 15px 3px;
       border-bottom-left-radius: 3px 15px;
       border: 3px solid transparent;
       font-family: "Architects Daugther", cursive;
       font-size: 1rem;
       color: hsla(260, 2%, 25%, 0.7);
       width: 70%;
       margin-bottom: 20px;
       border-bottom: solid black;
   }
   button{
       padding: 0;
       border: none;
       font-family: "Architects Daugther", cursive;
       text-decoration: none;
       padding-bottom: 3px;
       padding-left: 3px;
       border-radius: 5px;
       background-color:transparent;
   }
   button > span{
       background: #f1f5f8;
       display: block;
       padding: 0.5rem 1rem;
       border-radius: 5px;
       border: 2px solid hsl(198, 1%, 29%);
   }
   li{
       text-align: left;
       position: relative;
       padding: 0.5rem;
   }